@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  color: white;
}

a {
  text-decoration: none;
}

input {
  outline: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.all_wrapper {
    display: grid;
    width: 100%;
    height: 100%;
    min-height: 800px;
    background: linear-gradient(124.8deg, rgba(0, 0, 0, 0.85) -10.84%, rgba(0, 0, 0, 0.7395) 102.17%);
    font-family: 'Montserrat', sans-serif;
}

.app-wrapper {
    margin: 0 auto;
    display: grid;
    max-width: 1240px;
    width: 1240px;
    grid-template-areas: "h h" "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 2px;
}

.app-wrapper-content {
    grid-area: c;
    background: rgba(53, 73, 67, 0.83);
}
.Navbar_nav__2ChFn {
    grid-area: n;
    padding: 10px;
    padding-left: 25%;
    background: linear-gradient(243.26deg, #C1199C -11.48%, rgba(193, 25, 156, 0.36) 84.09%);
}

.Navbar_item__39T1q {
    margin-top: 15px;
}

.Navbar_item__39T1q a {
    font-size: 22px;
    text-decoration: none;
    font-weight: 400;
    font-size: 18px;
    color: white;
    transition: all 200ms;
}

.Navbar_item__39T1q a.Navbar_activeLink__M0U0Q {
    border-left: 3px solid black;
    padding-left: 5px;
    color: black;
}

.Navbar_item__39T1q a:hover {
    padding-left: 5px;
    transition: all 200ms;
    color: thistle;
}










